<template>
  <div v-if="issee" class="musicaggr" @touchstart="startblack" @touchmove="moveblack">
    <!-- 分为三个组件 -->
    <AggrTop :aggrdata="aggrdata" />
    <AggrMain
      :aggrsong="aggrsong"
      :activemusic="activemusic"
      :paused="paused"
      :isicon="isicon"
      @update:activemusic="$emit('update:activemusic', $event)"
      @aggrallsong="$emit('aggrallsong', aggrsong)"
    />
  </div>
</template>

<script>
import AggrTop from "@/components/Aggrtop.vue";
import AggrMain from "@/components/Aggrmain.vue";

export default {
  props: {
    activemusic: Object,
    paused: Boolean,
  },
  data() {
    return {
      id: null,
      aggrsong: [],
      aggrdata: [],
      issee: false,
      isicon: false,
      startX: 0,
    };
  },
  methods: {
    startblack: function(e){
      this.startX = e.touches[0].clientX;
    },
    moveblack: function(e){
      let num =  e.touches[0].clientX - this.startX;

      if(num > 150 && this.$route.fullPath != "/"){

        this.$router.push({
          path: "/"
        });

      }
    }
  },
  components: {
    AggrTop,
    AggrMain,
  },
  watch: {
    id: function (n) {
      this.issee = false;
      // 请求歌单详情
      this.axios("/playlist/detail?id=" + n).then((response) => {
        // console.log(response.data.playlist.trackIds.map(item => item.id).join());

        let str = response.data.playlist.trackIds.map((item) => item.id).join();
        // 用歌曲ID请求歌曲详情
        this.axios("/song/detail?ids=" + str).then((resp) => {
          // console.log(resp.data.songs);

          this.aggrdata = response.data.playlist;
          this.aggrsong = resp.data.songs.map((item) => {
            // return item.song.artists = item.ar;
            item["picUrl"] = item.al.picUrl;

            item["song"] = {};
            item.song["artists"] = item.ar;
            return item;
          });

          this.issee = true;
          this.$root.isshowloading = false;
        });
      });
    },
  },
  created: function () {
    // console.log(this.$route.query.id);
    this.id = this.$route.query.id;
  },
  activated: function () {
    // console.log(this.$route.query.id);
    this.id = this.$route.query.id;
  },
  beforeRouteUpdate: function () {
    // console.log(this.$route.query.id);
    this.id = this.$route.query.id;
  },
};
</script>

