<template>
  <div class="searchresult">
    <div class="searchloading" v-if="isloading">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="datanot" v-else-if="isnot">抱歉,找不到该曲目</div>
    <div class="alldata" v-else>
      <div class="best">
        <h4>最佳匹配</h4>
        <SearchResultBestItem
          v-for="(item, index) in bestdata.orders"
          :key="index"
          :itemtype="bestdata[item]"
        />
      </div>
      <ul>
        <li v-for="(item, index) in searchmusic" :key="index">
          <NewestSongItem
            :itemdata="item"
            :activemusic="activemusic"
            :paused="paused"
            :isicon="isicon"
            @update:activemusic="$emit('update:activemusic', { item, index })"
            @aggrallsong="$emit('aggrallsong')"
          />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import NewestSongItem from "./NewestSongItem";
import SearchResultBestItem from "./SearchResultBestItem";

export default {
  props: [
    "searchmusic",
    "activemusic",
    "paused",
    "isicon",
    "isnot",
    "isloading",
    "bestdata",
  ],
  components: {
    NewestSongItem,
    SearchResultBestItem,
  },
};
</script>

<style lang="less">
.searchresult {
  .searchloading {
    height: 300px;
    text-align: center;

    img {
      margin-top: 130px;
    }
  }

  .datanot {
    width: 100%;
    height: 320px;
    color: #999;
    font-size: 30px;
    font-family: "楷体";
    text-align: center;
    line-height: 240px;
  }

  .best {
    margin-top: 12px;

    h4 {
      color: #666;
      font-size: 12px;
      line-height: 16px;
      padding-left: 10px;
    }
  }
}
</style>