<template>
  <div class="hotmusic" style="margin-top: 124px">
    <div class="hot-title">
      <div class="hotimg"></div>
      <p>更新日期：12月29日</p>
    </div>
    <div class="hot-aggr">
      <ul>
        <li v-for="(item, index) in hotaggrmusic" :key="index">
          <p v-text="index < 9 ? '0' + (index + 1) : index + 1"></p>
          <SongAggrItem
            :itemdata="item"
            :activemusic="activemusic"
            :paused="paused"
            :isicon="isicon"
            @update:activemusic="$emit('update:activemusic', { item, index })"
            @aggrallsong="$emit('aggrallsong', hotaggrmusic)"
          />
        </li>
      </ul>
    </div>
    <div class="allsong">查看完整榜单 ></div>
  </div>
</template>

<script>
import SongAggrItem from "@/components/NewestSongItem.vue";

export default {
  props: ["activemusic", "paused","isicon"],
  data: function () {
    return {
      hotaggrmusic: [],
    };
  },
  methods: {},
  components: {
    SongAggrItem,
  },
  created: function () {
    this.$root.isshowloading = true;

    this.axios("/top/list?idx=1").then((response) => {
      // console.log(response.data.playlist.trackIds.slice(0, 20));
      // console.log(response.data.playlist.trackIds.slice(0,20).map(item=> item.id).join());
      console.log(response.data);
      let ids = response.data.playlist.trackIds
        .slice(0, 20)
        .map((item) => item.id)
        .join();

      this.axios("/song/detail?ids=" + ids).then((response) => {
        // console.log(response.data.songs);
        // response.data.songs.song.artists = response.data.songs.ar;
        this.hotaggrmusic = response.data.songs.map((item) => {
          // return item.song.artists = item.ar;
          item["picUrl"] = item.al.picUrl;

          item["song"] = {};
          item.song["artists"] = item.ar;
          return item;
        });

        this.$root.isshowloading = false;
      });

    });
  },
};
</script>

