<template>
  <div class="aggrtop">
    <div class="left-img">
      <img :src="aggrdata.coverImgUrl" />
      <i>歌单</i>
      <span class="peopleNum">{{ peopleNum }}</span>
    </div>
    <div class="right-introduce">
      <h2>{{ aggrdata.name }}</h2>
      <div class="nick">
        <a href="#">
          <i :style="{ background: nickimg, 'background-size': '100% 100%' }"></i>
          <span>{{ aggrdata.creator.nickname }}</span>
        </a>
      </div>
    </div>
    <i
      id="aggr-bg"
      :style="{
        background: aggrtopimg,
        backgroundSize: 'cover',
        backgroundPosition: ' 50%',
      }"
    ></i>
  </div>
</template>

<script>
export default {
  props: ["aggrdata"],
  computed: {
    peopleNum() {
      return (this.aggrdata.playCount / 10000).toFixed(1) + "万";
    },
    aggrtopimg() {
      return 'url("' + this.aggrdata.coverImgUrl + '") no-repeat';
    },
    nickimg() {
      return 'url("' + this.aggrdata.creator.avatarUrl + '") no-repeat';
    },
  },
};
</script>

<style scoped lang="less">
@media screen and (min-width: 360px) {
  div.aggrtop div.left-img {
    width: 126px;
    height: 126px;
  }
}
@media screen and (min-width: 414px) {
  div.aggrtop div.left-img {
    width: 145px;
    height: 145px;
  }
}
@media screen and (min-width: 768px) {
  div.aggrtop div.left-img {
    width: 200px;
    height: 200px;
  }
}

.aggrtop {
  display: flex;

  position: relative;
  padding: 30px 10px 30px 15px;
  overflow: hidden;
  font: 14px/1.5 Helvetica, sans-serif, STHeiTi;

  #aggr-bg {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    filter: blur(20px);
    transform: scale(1.5);
  }

  .left-img {
    position: relative;
    width: 114px;
    height: 114px;
    background-color: #e2e2e3;

    &::after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 18px;
      z-index: 2;
      background-image: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0),
        rgba(0, 0, 0, 0.2)
      );
    }

    img {
      vertical-align: middle;
    }

    i {
      position: absolute;
      z-index: 3;
      top: 10px;
      left: 0;
      padding: 0 8px;
      height: 17px;
      color: #fff;
      font-size: 9px;
      text-align: center;
      line-height: 17px;
      background-color: rgba(217, 48, 48, 0.8);
      border-top-right-radius: 17px;
      border-bottom-right-radius: 17px;
      font-style: normal;
    }

    .peopleNum {
      position: absolute;
      right: 2px;
      top: 0;
      z-index: 3;
      padding-left: 15px;
      color: #fff;
      font-size: 12px;
      background-image: url("data:image/svg+xml;base64,");
      background-position: 0;
      background-repeat: no-repeat;
      background-size: 11px 10px;
      text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
    }
  }
  .right-introduce {
    flex: 1 1 auto;
    width: 1%;
    margin-left: 16px;

    h2 {
      margin-bottom: 20px;
      padding-top: 1px;
      font-size: 17px;
      line-height: 1.3;
      color: #fefefe;
      height: 44px;
    }

    .nick {
      a {
        display: block;
        color: hsla(0, 0%, 100%, 0.7);
        text-decoration: none;

        i {
          display: inline-block;
          width: 30px;
          height: 30px;
          margin-right: 5px;
          border-radius: 50%;
          vertical-align: middle;
        }
      }
    }
  }
}
</style>