<template>
  <div class="searchbox" style="margin-top: 124px">
    <div class="inp">
      <b class="iconfont">&#xe69e;</b>
      <input
        type="text"
        @focus="panduan"
        @input="shuru"
        @keyup.enter="searchtext"
        ref="inptext"
        placeholder="搜索歌曲、歌手、专辑"
      />
      <span @click="qingkong"><i>×</i></span>
    </div>

    <keep-alive>
      <component
        :is="activetab"
        :hotlist="hotlist"
        :searchkey="searchkey"
        :searchproposal="searchproposal"
        :searchmusic="searchmusic"
        :activemusic="activemusic"
        :searchhistory="searchhistory"
        :paused="paused"
        :isicon="isicon"
        :isnot="isnot"
        :isloading="isloading"
        :bestdata="bestdata"
        @update:activemusic="$emit('update:activemusic', $event)"
        @aggrallsong="$emit('aggrallsong', searchmusic)"
        @searchtext="songitemclick"
        @startsearch="searchtext"
        @clearhistory="searchhistory.splice($event, 1)"
      ></component>
    </keep-alive>
  </div>
</template>

<script>
import SearchHome from "@/components/SearchHome.vue";
import SearchPreview from "@/components/SearchPreview.vue";
import SearchResult from "@/components/SearchResult.vue";

export default {
  props: ["activemusic", "paused", "isicon"],

  components: {
    SearchHome,
    SearchPreview,
    SearchResult,
  },

  data: () => {
    return {
      id: 0,
      timer: null,
      activeid: 0,
      alltab: ["SearchHome", "SearchPreview", "SearchResult"],
      hotlist: null,
      searchkey: "",
      searchproposal: [],
      searchmusic: [],
      searchhistory: [],
      isnot: false,
      isloading: false,
      bestdata: {},
    };
  },

  methods: {
    panduan: function (e) {
      if (e.target.value) this.activeid = 1;
    },
    shuru: function (e) {
      // 判断文本框是否为空。
      if (e.target.value == "") {
        this.activeid = 0;
        return;
      }

      // 切换组件，并记录输入框的值
      this.activeid = 1;
      this.searchkey = e.target.value;

      if (this.timer != null) window.clearTimeout(this.timer);

      this.timer = window.setTimeout(() => {
        if (e.target.value == "") return;

        this.axios(
          "/search/suggest?keywords=" + e.target.value + "&type=mobile"
        ).then((response) => {
          this.searchproposal = response.data.result.allMatch;
        });
      }, 300);
    },
    searchtext: function () {
      this.isnot = false;
      this.isloading = true;
      this.searchmusic = [];

      this.axios(
        "/search/multimatch?keywords=" + this.searchkey + "&type=mobile"
      ).then((reser) => {

        reser.data.result.orders = reser.data.result.orders.filter((item) => {
          return item == ("album" || "artist");
        });

        if (
          reser.data.result["album"] &&
          reser.data.result.orders.indexOf("album") == -1
        ) {
          reser.data.result.orders.push("album");
        }
        if (
          reser.data.result["artist"] &&
          reser.data.result.orders.indexOf("artist") == -1
        ) {
          reser.data.result.orders.unshift("artist");
        }

        this.bestdata = reser.data.result;
      });

      this.axios("/search?keywords=" + this.searchkey).then((response) => {
        // this.axios("/search?keywords="+this.searchkey+"&type=100").then(response=>{
        //   console.log(response);
        // });

        if (response.data.result.songs == undefined) {
          this.isnot = true;
          console.log("找不到该曲目");
          this.isloading = false;
          return;
        }

        let ids = response.data.result.songs.map((item) => item.id).join();

        this.axios("/song/detail?ids=" + ids).then((response) => {
          this.searchmusic = response.data.songs.map((item) => {
            item["picUrl"] = item.al.picUrl;

            item["song"] = {};
            item.song["artists"] = item.ar;
            return item;
          });
        });

        this.isloading = false;
      });

      this.activeid = 2;

      this.searchhistory = this.searchhistory.filter((item) => {
        return item.name != this.searchkey;
      });

      this.searchhistory.unshift({ id: this.id++, name: this.searchkey });
    },
    qingkong: function () {
      this.activeid = 0;
      this.$refs.inptext.value = "";
    },
    songitemclick: function (text) {
      this.searchkey = text;
      this.searchtext();
      this.$refs.inptext.value = text;
    },
  },

  computed: {
    activetab: function () {
      return this.alltab[this.activeid];
    },
  },

  created: function () {
    this.$root.isshowloading = true;

    this.axios("/search/hot").then((response) => {
      this.hotlist = response.data.result.hots;

      this.$root.isshowloading = false;
    });
  },
};
</script>

<style scoped lang="less">
.inp {
  padding: 15px 10px;
  display: flex;
  line-height: 30px;
  border-bottom: 1px solid transparent;
  box-shadow: 0 0.1px 0 0 grey;

  b {
    width: 30px;
    height: 30px;
    border-top-left-radius: 18px;
    border-bottom-left-radius: 18px;
    background: #ebecec;
    text-align: center;
    font-size: 12px;
    font-weight: normal;
  }
  input {
    outline: none;
    border: none;

    flex-grow: 1;
    background: #ebecec;
  }
  span {
    display: block;
    width: 30px;
    height: 30px;
    background: #ebecec;
    border-top-right-radius: 18px;
    border-bottom-right-radius: 18px;

    i {
      display: block;
      width: 16px;
      height: 16px;

      color: rgb(245, 239, 239);
      text-align: center;
      line-height: 16px;
      text-indent: -1px;

      background: #ccc;
      border-radius: 50%;
      margin-top: 7px;
      margin-left: 5px;
    }
  }
}
</style>